<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Extension Resize Event</h2>
    <ul>
      <li><a href="http://wowgoo.cn" target="_blank">不管你怎么拽，页面窗口的宽度一次性变成这样啦<span>({{width}}px)</span></a></li>
      <li><a href="http://wowgoo.cn" target="_blank">不管你怎么拖，页面窗口的高度一次性变成这样啦<span>({{height}}px)</span></a></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloResize',
  data () {
    return {
      msg: 'Welcome to Use jQuery.newresize.js',
      width:'',
      height:''
    }
  },
  methods:{
    autoResize(){
      this.width= $(window).width();
      this.height= $(window).height();
    } 

  },
  mounted(){
    this.autoResize();
    $(window).newresize(this.autoResize,200);
    //参数说明(resize方法，resize一次的时间)
  },
  destroyed(){
    $(window).unbind('resize');
    //离开组件或者不需要执行resize时间时销毁事件
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #0085f6;
    text-decoration: none
}
a span{
  font-size: 30px;
  font-weight: 700;
  color:rgb(120, 214, 245);

}
</style>



